import * as History from 'history';
export const getCompatibleHref = (to) => {
    if (typeof to === 'string') {
        return to;
    }
    if (typeof to === 'object' && to !== null) {
        return History.createPath(to);
    }
    return '';
};
export const getCompatibleClassName = (className) => {
    if (typeof className === 'function') {
        return className({ isActive: false });
    }
    if (typeof className === 'string') {
        return className;
    }
    return undefined;
};
export const getCompatibleStyle = (style) => {
    if (typeof style === 'function') {
        return style({ isActive: false });
    }
    return style;
};
export const getCompatibleChildren = (children) => {
    if (typeof children === 'function') {
        return children({ isActive: false });
    }
    return children;
};
